/* 现有推广思路组件 */
function PromotionIdeas() {
  return (
    <Box mt={2}>
      <Typography variant="h6">现有情况下怎么推广</Typography>
      <Stack spacing={0.5}>
        <Typography variant="body1">
          • 现在产品体验依然不好，所以想了解下诸位对现有产品的感受？无论有什么样的改进方式都请提出。
        </Typography>
        <Typography variant="body1">• 如果能接受进行推广，那么我们应该怎么推广？</Typography>
        <Typography variant="body1">• 去广场舞的地方进行推广，有哪些套路可用？</Typography>
        <Typography variant="body1">
          • 一些加班、出差、、应酬客人工作，由其是女性，是否可以作为公司的关怀谈下来？
        </Typography>
        <Typography variant="body1">• 外卖、快递群体能否跟大厂作为员工福利谈下来？</Typography>
        <Typography variant="body1">
          • 到抖音找人推广，怎么找？找什么人？我是个彻底的抖音盲，这个方向一定得你们出主意。
        </Typography>
        <Typography variant="body1">
          •
          到写字楼找等电梯的人进行推广，我去咕咚躺地上表演，旁白你们帮我录像，最终找几个合适的视频可以用作宣传，但是可能得给人家打码。其实刷楼扫楼也可以，但是用什么方法给他们洗脑呢？
        </Typography>
        <Typography variant="body1">• 还有哪些比较精准小范围的推广？就是用户反馈能比较多，比较直接的那种</Typography>
        <Typography variant="body1">
          • 营销有什么创造神秘感，或者饥饿营销的想法不？这种方向能找到方式或许会成为非常好的低成本扩张方案。
        </Typography>
        <Typography variant="body1">
          • 我们产品有个巨大的亮点，我一直不会描述。功能是<br/>
          普通遇到危急，假设发病了，但是还能有点神智，不过已经开始逐渐进入昏迷。
          这时他给一个亲人，例如儿子打电话，但是儿子的电话没接，原因可能是多样的，例如手机没在手头、睡觉、开会静音等，而作为求助者是不知道的，所以他不知道自己究竟是挂断给其他亲人打电话还是继续等待儿子接电话。<br/>
          而我们的应用是，只要向我们的后台发出消息了，我们会同时给他的亲人都发送短信，拨打电话，发送位置信息，拉起音视频会议。
          这点在ios和安卓都是没有的。
        </Typography>
      </Stack>
    </Box>
  );
}

/* 解决的痛点组件 */
function PainPointHighlight() {
  return (
    <Box mt={2}>
      <Typography variant="h6">实操中解决的痛点，但是刚接触容易忽略</Typography>
      <Stack spacing={1} mt={1}>
        <Typography variant="body1">
          在危急情况下，求助者往往第一反应是打电话给最亲近的人。
        </Typography>
        <Typography variant="body1">
          但是现实中，这个电话可能因为无人接听（手机不在身边、睡觉、开会静音等）而失效。
        </Typography>
        <Typography variant="body1">
          此时，求助者本人并不知道该继续等待，还是挂断再打给其他人。
          <b>这种不确定性在危急时刻是致命的。</b>
        </Typography>
        <Typography variant="body1">
          我们的产品从根本上解决了这一问题：一旦用户在应用中发出求助消息，后台会自动执行一系列动作：
        </Typography>

        <Stack spacing={0.5} mt={1}>
          <Stack direction="row" spacing={1} alignItems="center">
            <Sms fontSize="small" />
            <Typography variant="body2">群发通知：同时发送短信提醒</Typography>
          </Stack>
          <Stack direction="row" spacing={1} alignItems="center">
            <Phone fontSize="small" />
            <Typography variant="body2">电话提醒：多位亲人被自动呼叫</Typography>
          </Stack>
          <Stack direction="row" spacing={1} alignItems="center">
            <LocationOn fontSize="small" />
            <Typography variant="body2">位置信息：第一时间同步位置</Typography>
          </Stack>
          <Stack direction="row" spacing={1} alignItems="center">
            <VideoCall fontSize="small" />
            <Typography variant="body2">
              即时音视频会议：家人第一时间进入沟通，远程指导施救
            </Typography>
          </Stack>
          <Stack direction="row" spacing={1} alignItems="center">
            <Security fontSize="small" />
            <Typography variant="body2">
              避免单点失败：不再依赖单个电话接通
            </Typography>
          </Stack>
        </Stack>

        <Typography variant="body1" mt={1}>
          这让家人不只是被动接到电话，而是<b>即时、全面地参与救援</b>，比传统电话机制更可靠。
        </Typography>
        <Typography variant="body1">
          目前在 iOS 和安卓的原生功能和已有 App 中，都没有这样的机制。
        </Typography>
      </Stack>
    </Box>
  );
}
